import Notify from '../../wxcomponents/vant/notify/notify.js'
import Toast from '../../wxcomponents/vant/toast/toast';
import clickCircle from '../../components/zwy/clickCircle.vue';

export default {
	components: {

		clickCircle
	},
	data() {
		return {
			show: true,
			modalName: null,
			realNameInfo: null,
			userInfo: {},
			wxUser: {},
			switchMsg:"",
		}
	},
	onLoad() {
		console.log("onLoad()")
		
	},
	onShow() {
		this.userInfo = uni.getStorageSync("userInfo")
		console.log("onShow()")

		if (!this.userInfo) {
			this.warnNotify()
		} else {
			// 每次都要更新的
			this.getUidAndSuper()
			// this.getRealName()

		}
	},
	methods: {
		SwitchSendMsg(e) {
			this.switchMsg = e.detail.value
			this.userInfo.switchMsg = this.switchMsg
			console.log("that.userInfo.switchMsg="+this.userInfo.switchMsg)
			if(this.userInfo.switchMsg==true){

			}
		},
		wakeUpMsg(){
			if(this.userInfo.switchMsg==true){
				wx.requestSubscribeMessage({
				  tmplIds: [
				    'jxm2u-gMXptrgJBsHOThe9-1L4Vw2dghG7J05FLEEKk',
				  ],
				  success(res) {
				    console.log(res)
				  },
				  fail(err) {
				    console.log(err);
				  }
				})
			}
		},
		conClick(e) {
			this.$refs.clickCircle.conClick(e);
		},
		warnNotify() {
			Notify({
				text: "未登录",
				duration: 1000,
				backgroundColor: 'blue',
				position: "bottom"
			})

		},
		successNotify(title) {
			Notify({
				backgroundColor: 'green',
				text: title,
				position: "bottom"
			});

		},
		onTowast(mes) {
			Toast("adad")
		},

		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},

		exitLogin() {
			var that = this;
			uni.showModal({
				title: "确定要退出吗",
				success: function(res) {
					if (res.confirm) {
						that.userInfo = null;
						uni.setStorageSync("userInfo", null);
						console.log(that.userInfo)
					}


				}
			})
		},
		getUidAndSuper() {
			console.log("that.userInfo.uId=" + this.userInfo)
			var that = this;
			uni.request({
				url: "https://api.lifeisgg.online/attendance/wxUser/login",
				data: {
					"openId": that.userInfo.openid,
					"avatarUrl": that.userInfo.avatarUrl,
					"nickName": that.userInfo.nickName
				},
				// header:{
				// 	"Content-Type":'applicantion/json'
				// },
				success(res) {

					let title = "";
					let icon = "success";
					console.log(res.data.success)
					that.userInfo.uId = res.data.wxUserInfo.uId
					that.userInfo.sSuper = res.data.wxUserInfo.sSuper
					uni.request({
						url: "https://api.lifeisgg.online/attendance/realName/getRealName",
						data: {
							"uId": that.userInfo.uId
						},
						success(res) {
							console.log(res.data)
							that.realNameInfo = res.data.realNameInfo
							that.userInfo.realNameInfo = res.data.realNameInfo

							uni.setStorageSync("userInfo", that.userInfo)
							that.userInfo = uni.getStorageSync("userInfo")

						}
					})

					if (res.data.success != false) {
						if (res.data.success == null) {
							if (res.data.wxUserInfo.sSuper == 0) {
								title = "欢迎回来超管"
							} else {
								title = "欢迎回来"
							}

						} else if (res.data.success == true) {
							title = "欢迎您加入岭师考勤系统"
						}

					} else {
						title = "失败",
							icon = "none"
					}
					that.successNotify(title)
					console.log(res)
				}
			})

		},

		getRealName() {
			var that = this;

			console.log("that.userInfo.uId=" + this.userInfo)
			uni.request({
				url: "https://api.lifeisgg.online/attendance/realName/getRealName",
				data: {
					"uId": that.userInfo.uId
				},
				success(res) {
					console.log(res.data)
					that.realNameInfo = res.data.realNameInfo
					that.userInfo.realNameInfo = res.data.realNameInfo

					uni.setStorageSync("userInfo", that.userInfo)
					that.userInfo = uni.getStorageSync("userInfo")

				}
			})
		},

		getUserinfo: function(res) {
			var that = this;
			if (!res.detail.iv) {
				uni.showToast({
					title: "求求你授权吧"
				})
				return false;
			}
			console.log(res)
			that.userInfo = res.detail.userInfo
			// 调用云函数获取openid
			wx.cloud.callFunction({
				name: 'login',
				data: {},
				success: res => {
					console.log('[云函数] [login] user openid: ', res.result.openid)
					that.userInfo.openid = res.result.openid
					that.switchMsg = true
					that.userInfo.switchMsg = true


					that.getUidAndSuper()
					// console.log("that.userInfo.sSper:::"+that.userInfo.sSper)
					// that.getRealName()
					// uni.setStorageSync("userInfo", that.userInfo)
				},
				fail: err => {
					console.error('[云函数] [login] 调用失败', err)
				}
			})

		},



		realNameSubmit: function(e) {
			var that = this;
			console.log(e.detail.value)
			let sId = e.detail.value.sId
			let pwd = e.detail.value.pwd
			let email = e.detail.value.email


			if ((!sId || !pwd) || !email) {
				// this.onTowast("请填写完整")
				uni.showToast({
					title: "请填写完整",
					icon: "none"
				})
				return false
			}
			uni.request({
				url: "https://api.lifeisgg.online/attendance/realName/tieRealName",
				data: {
					"uId": that.userInfo.uId,
					"sId": e.detail.value.sId,
					"password": e.detail.value.pwd,
					"email": e.detail.value.email
				},
				success(res) {
					let title = "";

					console.log(res.data.success)
					let msg = res.data.success;
					if (msg == "tied") {
						title = "此学号已绑定"
					} else if (msg == "noExist") {
						title = "账号不存在或密码错误"
					} else if (msg == true) {
						title = "绑定成功"
						that.hideModal()
						that.getRealName()
					} else {
						title = "绑定失败"
					}
					uni.showToast({
						title: title,
						icon: "none"
					})
					that.successNotify(title)
				}
			})

		},
		
		sendMsg: function(e) {
		    wx.requestSubscribeMessage({
		      tmplIds: [
		        'il9K_JqyWPEIKbIHaSMHMvRM3BYAMt_UC6gCVERpXjU',
		      ],
		      success(res) {
		        console.log(res)
		      },
		      fail(err) {
		        console.log(err);
		      }
		    })
		  },
		
		    getMsg: function() {
		      wx.cloud.callFunction({
		        name: 'sendMsg',
		        data: {
		        },
		        success(res) {
		          console.log(res);
		          wx.showToast({
		            title: '发送成功!',
		          })
		
		        },
		        fail(err) {
		          console.log(err);
		          wx.showToast({
		            title: '发送失败!',
		          })
		        }
		      })
		    },

	}
}
